%line {
    content: '';
    position: absolute;
    background: #fff;
    width: 0;
    bottom: 0;
}

%container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

$color: #fff;
$text-transform: uppercase;
$user-select: none;

@mixin transition($duration) {
    transition: #{$duration ease}
};

@mixin animate($animation, $duration, $method) {
    animation: $animation $duration $method;
};

@mixin keyframes($name) {
    @keyframes #{$name} {
        @content
    }
};


// Button back

.btn-container {
    position: absolute;
    padding: 5px 0;
    top: 5%;
    left: 0;
    z-index: 99;
    
    .btn-back {
        $padding-right: 10px;
        
        text-transform: $text-transform;
        padding-right: $padding-right;
        user-select: $user-select;
        text-decoration: none;
        font-size: 25px;
        letter-spacing: 1px;
        color: black;
        
        .btn-arrow-icon {
            padding-right: $padding-right;
            height: 20px;
        }

        &::before {
            @extend %line;
            @include transition(.3s);
            @include animate(ani-btn, 1s, forwards);
            border-top-right-radius: 8px;
            border-bottom-right-radius: 8px;
            left: 0;
            width: 27%;
            height: 100%;
            z-index: -1;
        }

        &:hover {

            &::before {
                width: 100%;
            }
        }

        @include keyframes(ani-btn) {
            0% {
                left: -50%;
            }
            100% {
                left: 0;
            }
        }
    }
}

// Main page

.main-container {
    @extend %container;

    .title-author {
        %style-text {
            text-transform: $text-transform;
            user-select: $user-select;
            letter-spacing: 3px;
            padding: 10px 0;
            white-space: nowrap;
        }

        display: flex;
        flex-direction: column;
        position: relative;
        text-align: center;
        width: 100%;
        margin-bottom: calc(100vh / 3);
        
        @media screen and (max-width: 650px) {
            margin-bottom: calc(100vh / 5);
        }

        .text-name {
            @extend %style-text;
            position: relative;
            font-size: 40px;
            font-weight: 600;
            margin-bottom: 10px;

            &::before {
                @extend %line;
                @include animate(name-animation, 1.5s, forwards);
                height: 100%;
                right: 0;
                z-index: -1;
            }

            @include keyframes(name-animation) {
                0% {
                    width: 0;
                }
                40% {
                    width: 0;
                }
                100% {
                    width: 100%;
                }
            }
        }
        
        .text-prof {
            @extend %style-text;
            @include animate(prof-animation, 2s, forwards);
            color: $color;
            position: absolute;
            font-size: 25px;
            right: 50%;
            transform: translateX(50%);
            opacity: 0;
            top: 0;

            @include keyframes(prof-animation) {
                0% {
                    opacity: 0;
                    top: 0;
                }
                50% {
                    opacity: 0;
                    top: 0;
                }
                100% {
                    opacity: 1;
                    top: 85%;
                }
            }
        }
    }

    .nav-container {
        display: flex;
        justify-content: space-around;
        align-items: center;
        list-style: none;
        padding: 0;

        @media screen and (max-width: 650px) {
            flex-direction: column;
        }

        .nav-items {
            display: flex;
            justify-content: space-around;
            align-items: center;
            position: relative;
            margin: 0 5px;
            width: 100%;
            height: 100%;

            .nav-link {
                @include transition(1s);
                @include animate(nav-animation, 1s, forwards);
                text-transform: $text-transform;
                user-select: $user-select;
                color: $color;
                font-size: 10px;
                text-decoration: none;
                letter-spacing: 1px;
                padding: 5px 0;
                filter: blur(10px); 
                opacity: 0; 
                z-index: 1;

                @include keyframes(nav-animation) {
                    0% {
                        opacity: 0; 
                        filter: blur(10px); 
                        font-size: 10px;
                        padding: 5px 0;
                    }
                    100% {
                        opacity: 1; 
                        filter: blur(0);
                        font-size: 30px;
                        padding: 5px 25px;
                    }
                }

                &::before {
                    @extend %line;
                    @include transition(.4s);
                    height: 50%;
                    left: 0;
                    bottom: 50%;
                    z-index: -1;
                }
                
                &::after {
                    @extend %line;
                    @include transition(.5s);
                    height: 50%;
                    right: 0;
                    z-index: -1;
                }
                
                &:hover {
                    color: #000;
                    
                    &::before, 
                    &::after {
                        width: 100%;
                    }
                }
            } 
        }
    }
};

// Contacts page

.contacts-container {
    @extend %container;
    overflow: hidden;

    .contact-block {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 5px;
        width: 100%;

        &:nth-of-type(even) {
            @include animate(ani-even, .8s, forwards);
            opacity: 0;
        }
        
        @include keyframes(ani-even) {
            0% {
                margin-right: 1500px;
                opacity: 0;
            }
            100% {
                margin-right: 0;
                opacity: 1;
            }
        }
        &:nth-of-type(odd) {
            @include animate(ani-odd, .8s, forwards);
            opacity: 0;
        }

        @include keyframes(ani-odd) {
            0% {
                margin-left: 1500px;
                opacity: 0;
            }
            100% {
                margin-left: 0;
                opacity: 1;
            }
        }

        .contact-logo {
            width: 65px;
        };
        
        .contact-text {
            text-align: center;
            font-size: 20px;
            color: #fff;
            width: 200px;
            text-decoration: none;
            white-space: nowrap;

            &:hover {
                    text-decoration: underline;
                    text-underline-offset: 10px;
            }
        };

    }
};

// Modal

.modal {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background: transparent;
    backdrop-filter: blur(5px);
    transform: scale(0);
    z-index: 999;

    &.active {
        transform: scale(1);
    }

    .modal-content {
        @include transition(.2s);
        position: relative;
        background: #96969675;
        padding: 25px;
        border-radius: 5px;
        transform: scale(0);

        &.active {
            transform: scale(1);
        }

        .close-btn-modal {
            position: absolute;
            top: 0;
            right: 0;
        }
    }
}




.about-container,
.skills-container,
.portfolio-container {
    @extend %container
};




h1 {
    color: #fff;
    text-transform: uppercase;
    animation: ani 3s forwards;
}

@keyframes ani {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.label-form {
    color: #fff;
}






